*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

#hdr{
  background-color: rgba(0, 0, 255, 0.863);
  color: white;
  text-align: center;
  padding: 10px;
  width: 100%;
  font-size: 1rem;
  font-weight: 500;
 
}

nav{
  background-color: white;
  padding: 30px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

nav a{
  margin: 20px;
  text-decoration: none;
}

.container{
  background-color:rgb(255, 225, 209);
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 40px 100px;
}

.container h1{
  text-align: center;
  margin: 20px;
}

.container form{
  display: flex;
  flex-direction: column;
  
}

input, textarea,button{
  padding: 15px;
  border: 1px solid rgba(0, 0, 0, 0.212);
  border-radius: 5px;
  margin: 10px 0px;
  font-size: 1.2rem;
}

button{
  border: none;
  cursor: pointer;
  background-color: black;
  color: white;
}

.task{
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0px;
}

.task > div{
  width: 100%;

}
.task span{
  color: rgba(0, 0, 0, 0.637);
}


.task > button{
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task > button:hover{
  background-color: red;
}


.about{
  height: 100vh;;
  width: 100%;
  background-color: blue;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 4rem;
  font-weight: 700;
}

.Contact{
  height: 100vh;;
  width: 100%;
  background-color: rgb(30, 30, 30);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: brown;
  font-size: 4rem;
  font-weight: 700;
}

.Contact ul{
  margin-top: 15px;

}

.Contact ul li{
  font-size: 2rem;
  font-weight: 400;
  display: block;
}